import React from 'react';

const StatisticItem = ({randomColor, stat: { id, label, percentage }}) => {
    return (<li className="stat-item" style={{backgroundColor: `rgb(${randomColor}, randomColor, randomColor)`}} key={id}>
    <span className="label">{label}</span>
    <span className="percentage">{percentage} %</span>
            </li>)
    
}

export default  StatisticItem 